import ReportComponent from '../components/home/ReportComponent.vue';
import ModalBasic from '../components/common/ModalBasic.vue';
import Notification from '../components/home/Notification.vue';
import Support from '../components/home/Support.vue';
import QA from '../components/home/QA.vue';
import config from '../config/config';

export default {
    layout: 'social',
    components: { ModalBasic, ReportComponent, Notification, Support, QA },
    head: {
        title: 'Trang Tổng Quan' + config.siteName,
            htmlAttrs: {
            lang: 'en'
        },
        meta: [
            { charset: 'utf-8' },
            { name: 'viewport', content: 'width=device-width,initial-scale=1.0,user-scalable=0,minimal-ui' },
            { hid: 'description', name: 'description', content: config.description },
            { hid: 'og:description', name: 'og:description', content: config.description },
            { hid: 'keywords', name: 'keywords', content: config.keyword },
            { hid: 'dc.keywords', name: 'dc.keywords', content: config.keyword },
            { hid: 'news_keywords', name: 'news_keywords', content: config.keyword },
            { hid: 'og:site_name', name: 'og:site_name', content: config.siteName2 },
            { hid: 'dc.title', name: 'dc.title', content: config.title },
            { hid: 'og:title', name: 'og:title', content: config.title },
            { name: 'format-detection', content: 'telephone=no' },
        ],
    },
    data() {
        return {
            user: this.$store.state.user,
            systemNotifies: [],
        }
    },
    methods: {
        async getSystemNotify() {
            try {
                let response = await this.$http.get('/v2/get-system-notify')
                this.systemNotifies = response.data.data
            } catch (error) {
                this.$notify.error({
                    title: 'Error',
                    message: error.response.data.message
                });
            }
        }
    },
    watch: {
        '$store.state.user': function() {
            this.user = this.$store.state.user
        }
    },
    mounted() {
        this.getSystemNotify()
    },
}
